html {
  font-size: 100%;
}

body {
  font-size: 0.9rem;
  color: #383e45;
}

a {
  text-decoration: none;
  color: #24292e;
}

img {
  max-width: 100%;
}

* {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

.l-wrapper {
  max-width: 960px;
  margin: 0 auto 100px auto;
  padding: 0 4%;
  text-align: center;
}

.l-header {
  display: flex;
  height: 60px;
  margin: 0 auto;
  align-items: center;
  justify-content: space-between;
}
.l-header__img {
  width: 120px;
  line-height: 1px;
  padding: 10px 0;
  display: block;
}

.l-footer {
  padding: 10px 0;
  font-size: 0.5rem;
  text-align: center;
}

.l-nav-list {
  display: flex;
  padding: 10px 0;
}
.l-nav-list__item {
  margin-left: 30px;
}
.l-nav-list__item:hover {
  opacity: 0.7;
}

.p-mainvisual {
  margin-bottom: 80px;
}
.p-mainvisual__img {
  object-fit: cover;
  width: 100%;
  height: 600px;
  max-width: 1920px;
}

@media (max-width: 600px) {
  .p-mainvisual__img {
    height: calc(100vh - 60px);
  }
}
.p-profile {
  display: flex;
  justify-content: center;
  align-items: center;
}
.p-profile__img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-right: 30px;
}
.p-profile__text {
  text-align: left;
}

@media (max-width: 600px) {
  .p-profile {
    flex-direction: column;
  }
  .p-profile__img {
    margin-right: 0;
  }
}
.p-contents-list {
  display: flex;
  justify-content: space-between;
}
.p-contents-list__item {
  width: 32%;
}

@media (max-width: 600px) {
  .p-contents-list {
    flex-direction: column;
  }
  .p-contents-list__item {
    width: 100%;
    margin-bottom: 30px;
  }
}
.c-title {
  display: inline-block;
  font-size: 2rem;
  text-align: center;
  margin-bottom: 60px;
  border-bottom: solid 1px #383e45;
}

.c-contents-title {
  font-size: 1rem;
  margin: 10px 0;
}